* {
background-color: #eee;
}
.main_todolist{
  padding-left: 35%;
  padding-top: 10%;
  background-color:#eee;
}
.main_todolist h1{
  padding-left: 10%;
  font-size: 50px;
  font-family: 'Roboto', sans-serif;
}
.main_input{
  width:30%;
  height: 5vh;
  font-size: 20px;
  border: 1px solid black;
  border-radius:5px ;
}
.main_input:focus{
  outline: none;
}

.main_button{
  width: 10%;
  margin: 20px;
  height: 5vh;
  font-size: 20px;
  background-color: aquamarine;
  border: 1px solid aquamarine;
}
.main_button:focus{
outline: none;
}
.main_button:hover{
  background-color: rgb(26, 224, 158);
}
.main_paragraph{
  font-size: 25px;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
}

@media ( max-width:600px){
  .main_todolist{
    padding-left: 0%;
  }
  .main_todolist h1{
    padding-left: 0%;
    text-align: center;
  }
  .main_todolist .main_paragraph{
    text-align: center;
  }
  .main_todolist .main_button{
    width: 100px;
  }
  .main_todolist .main_input{
    width: 200px;
    margin-left: 20px;
  }
}